"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { User } from "lucide-react";

export default function SpotLight() {
  const [spotlightPosts, setSpotlightPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchSpotlightPosts();
  }, []);

  const fetchSpotlightPosts = async () => {
    try {
      setLoading(true);
      const response = await fetch("/api/public/posts?limit=4");
      if (!response.ok) throw new Error("Failed to fetch");
      
      const data = await response.json();
      setSpotlightPosts(data.posts || []);
    } catch (error) {
      console.error("Error fetching spotlight posts:", error);
    } finally {
      setLoading(false);
    }
  };

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  if (loading) {
    return (
      <section className="bg-gray-100 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex gap-8">
            <div className="w-20 h-64 bg-gray-200 dark:bg-slate-700 rounded"></div>
            <div className="flex-1 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              {[1, 2, 3, 4].map((i) => (
                <div key={i} className="h-96 bg-gray-200 dark:bg-slate-700 rounded"></div>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  if (spotlightPosts.length === 0) {
    return null;
  }

  return (
    <section className="bg-gray-100 dark:bg-slate-800 border-b border-gray-200 dark:border-slate-700 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex flex-col lg:flex-row gap-8 lg:gap-12">
          {/* SpotLight Title - Left Side (Vertical) */}
          <div className="flex-shrink-0 lg:w-20 flex items-center justify-center lg:justify-start">
            <h2 
              className="text-6xl lg:text-7xl font-bold text-black dark:text-white leading-none tracking-tight"
              style={{ 
                writingMode: 'vertical-rl',
                textOrientation: 'upright'
              }}
            >
              Spot
              <span className="relative inline-block">
                Light
                <span className="absolute -top-1 -right-6 lg:-right-8 w-3 h-3 bg-red-600 dark:bg-red-500 rounded-full"></span>
              </span>
            </h2>
          </div>

          {/* Article Cards - Right Side */}
          <div className="flex-1 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {spotlightPosts.map((post, index) => (
              <Link
                key={post._id || index}
                href={`/post?slug=${post.slug}`}
                className="group bg-white dark:bg-slate-900 rounded-lg overflow-hidden shadow-sm hover:shadow-lg transition-shadow duration-300"
              >
                {/* Image */}
                <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-700">
                  {post.featuredImage ? (
                    <Image
                      src={post.featuredImage}
                      alt={post.title}
                      fill
                      className="object-cover group-hover:scale-105 transition-transform duration-500"
                      sizes="(max-width: 768px) 100vw, (max-width: 1024px) 50vw, 25vw"
                    />
                  ) : (
                    <div className="w-full h-full bg-gradient-to-br from-blue-500 to-blue-600 dark:from-blue-600 dark:to-blue-700 flex items-center justify-center">
                      <span className="text-white text-sm font-medium">No Image</span>
                    </div>
                  )}
                </div>

                {/* Content */}
                <div className="p-5">
                  {/* Category */}
                  <div className="flex items-center gap-2 mb-3">
                    <span className="w-2 h-2 bg-black dark:bg-white rounded-sm"></span>
                    <span className="text-xs font-medium text-gray-700 dark:text-gray-300 uppercase tracking-wide">
                      {post.category || "News"}
                    </span>
                  </div>

                  {/* Title */}
                  <h3 className="text-lg font-bold text-black dark:text-white leading-snug mb-3 group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors line-clamp-3">
                    {post.title}
                  </h3>

                  {/* Excerpt */}
                  {post.excerpt && (
                    <p className="text-sm text-gray-600 dark:text-gray-400 leading-relaxed mb-4 line-clamp-3">
                      {post.excerpt}
                    </p>
                  )}

                  {/* Author */}
                  <div className="flex items-center gap-2 text-sm text-gray-700 dark:text-gray-300">
                    <User size={14} className="text-gray-500 dark:text-gray-400" />
                    <span className="font-medium">{getAuthorName(post.author)}</span>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
