"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { User } from "lucide-react";

export default function Featured() {
  const [featuredPosts, setFeaturedPosts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchFeaturedPosts();
  }, []);

  const fetchFeaturedPosts = async () => {
    try {
      setLoading(true);
      const response = await fetch("/api/public/posts?limit=15");
      if (!response.ok) throw new Error("Failed to fetch");
      
      const data = await response.json();
      setFeaturedPosts(data.posts || []);
    } catch (error) {
      console.error("Error fetching featured posts:", error);
    } finally {
      setLoading(false);
    }
  };

  const getAuthorName = (author) => {
    if (!author) return "Editorial Team";
    if (typeof author === "object" && author?.name) {
      return author.name;
    }
    return author;
  };

  // Split posts into three categories
  const smbPosts = featuredPosts.slice(0, 3);
  const herStoryPosts = featuredPosts.slice(3, 8);
  const socialStoryPosts = featuredPosts.slice(8, 13);

  if (loading) {
    return (
      <section className="bg-white dark:bg-slate-900 border-b border-gray-200 dark:border-slate-800 py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="animate-pulse">
            <div className="h-16 bg-gray-200 dark:bg-slate-700 mb-8 rounded"></div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              {[1, 2, 3].map((i) => (
                <div key={i} className="space-y-6">
                  <div className="h-12 bg-gray-200 dark:bg-slate-700 rounded"></div>
                  <div className="h-64 bg-gray-200 dark:bg-slate-700 rounded"></div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  if (featuredPosts.length === 0) {
    return null;
  }

  return (
    <section className="bg-white dark:bg-slate-900 border-b border-gray-200 dark:border-slate-800 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Featured Header */}
        <div className="flex items-center gap-4 mb-12">
          <div className="flex-shrink-0 relative">
            {/* Silhouette of person pulling rope */}
            <svg className="w-20 h-20 text-black dark:text-white" viewBox="0 0 100 100" fill="currentColor">
              {/* Person silhouette */}
              <circle cx="30" cy="25" r="8" />
              <rect x="22" y="33" width="16" height="30" rx="2" />
              <rect x="18" y="50" width="8" height="20" rx="2" />
              <rect x="34" y="50" width="8" height="20" rx="2" />
              {/* Rope */}
              <line x1="38" y1="40" x2="70" y2="50" stroke="currentColor" strokeWidth="3" strokeLinecap="round" />
              <line x1="70" y1="50" x2="90" y2="45" stroke="currentColor" strokeWidth="3" strokeLinecap="round" />
            </svg>
          </div>
          <h2 className="text-5xl md:text-6xl font-bold text-black dark:text-white">Featured</h2>
        </div>

        {/* Three Columns */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {/* Column 1: SMB Story */}
          <div className="space-y-6">
            <div className="bg-black dark:bg-white px-4 py-3">
              <h3 className="text-white dark:text-black font-bold text-lg uppercase tracking-wide">SMB Story</h3>
            </div>

            {/* Main Article - Black Box */}
            {smbPosts[0] && (
              <Link href={`/post?slug=${smbPosts[0].slug}`} className="group block">
                <div className="bg-black dark:bg-slate-800 p-6 text-white dark:text-white">
                  <h4 className="text-xl font-bold mb-3 group-hover:text-blue-400 transition-colors line-clamp-2">
                    {smbPosts[0].title}
                  </h4>
                  {smbPosts[0].excerpt && (
                    <p className="text-sm text-gray-300 dark:text-gray-300 mb-4 line-clamp-3 leading-relaxed">
                      {smbPosts[0].excerpt}
                    </p>
                  )}
                  <div className="text-sm text-gray-400 dark:text-gray-400">
                    {getAuthorName(smbPosts[0].author)}
                  </div>
                </div>
              </Link>
            )}

            {/* Other Articles */}
            {smbPosts.slice(1).map((post, index) => (
              <Link
                key={post._id || index}
                href={`/post?slug=${post.slug}`}
                className="group block pb-6 border-b border-gray-200 dark:border-slate-700 last:border-0"
              >
                <h4 className="text-base font-bold text-black dark:text-white mb-2 group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors line-clamp-2">
                  {post.title}
                </h4>
                <div className="text-sm text-gray-600 dark:text-gray-400">
                  {getAuthorName(post.author)}
                </div>
              </Link>
            ))}
          </div>

          {/* Column 2: Her Story */}
          <div className="space-y-6">
            <div className="bg-black dark:bg-white px-4 py-3">
              <h3 className="text-white dark:text-black font-bold text-lg uppercase tracking-wide">Her Story</h3>
            </div>

            {herStoryPosts.map((post, index) => (
              <Link
                key={post._id || index}
                href={`/post?slug=${post.slug}`}
                className="group flex gap-4 pb-6 border-b border-gray-200 dark:border-slate-700 last:border-0"
              >
                <div className="flex-1 min-w-0">
                  <h4 className="text-base font-bold text-black dark:text-white mb-2 group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors line-clamp-2">
                    {post.title}
                  </h4>
                  <div className="text-sm text-gray-600 dark:text-gray-400">
                    {getAuthorName(post.author)}
                  </div>
                </div>
                <div className="flex-shrink-0 w-20 h-20 rounded overflow-hidden bg-gray-100 dark:bg-slate-800">
                  {post.featuredImage ? (
                    <Image
                      src={post.featuredImage}
                      alt={post.title}
                      width={80}
                      height={80}
                      className="w-full h-full object-cover group-hover:opacity-90 transition-opacity"
                    />
                  ) : (
                    <div className="w-full h-full bg-gradient-to-br from-pink-400 to-pink-500 dark:from-pink-600 dark:to-pink-700"></div>
                  )}
                </div>
              </Link>
            ))}
          </div>

          {/* Column 3: Social Story */}
          <div className="space-y-6">
            <div className="bg-black dark:bg-white px-4 py-3">
              <h3 className="text-white dark:text-black font-bold text-lg uppercase tracking-wide">Social Story</h3>
            </div>

            {/* Main Article with Large Image */}
            {socialStoryPosts[0] && (
              <Link href={`/post?slug=${socialStoryPosts[0].slug}`} className="group block">
                <div className="relative aspect-video overflow-hidden bg-gray-100 dark:bg-slate-800 mb-3">
                  {socialStoryPosts[0].featuredImage ? (
                    <Image
                      src={socialStoryPosts[0].featuredImage}
                      alt={socialStoryPosts[0].title}
                      fill
                      className="object-cover group-hover:scale-105 transition-transform duration-500"
                      sizes="(max-width: 768px) 100vw, 33vw"
                    />
                  ) : (
                    <div className="w-full h-full bg-gradient-to-br from-green-400 to-green-500 dark:from-green-600 dark:to-green-700"></div>
                  )}
                  <div className="absolute top-4 left-4">
                    <span className="inline-flex items-center justify-center w-8 h-8 bg-red-600 dark:bg-red-500 text-white font-bold rounded-full text-sm">
                      1
                    </span>
                  </div>
                  <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 to-transparent p-4">
                    <h4 className="text-white font-bold text-lg line-clamp-2">
                      {socialStoryPosts[0].title}
                    </h4>
                  </div>
                </div>
              </Link>
            )}

            {/* Other Articles with Numbers */}
            {socialStoryPosts.slice(1).map((post, index) => (
              <Link
                key={post._id || index}
                href={`/post?slug=${post.slug}`}
                className="group flex gap-3 pb-6 border-b border-gray-200 dark:border-slate-700 last:border-0"
              >
                <span className="flex-shrink-0 inline-flex items-center justify-center w-6 h-6 bg-red-600 dark:bg-red-500 text-white font-bold rounded-full text-xs">
                  {index + 2}
                </span>
                <h4 className="text-base font-bold text-black dark:text-white group-hover:text-blue-600 dark:group-hover:text-blue-400 transition-colors line-clamp-2">
                  {post.title}
                </h4>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
